import React, { Component } from "react";
import { SwiperSlide } from "swiper/react";
import { Box, Card, CardContent, CardMedia, Grid, Typography } from "@material-ui/core";
import axios from "axios";
import { Skeleton } from "@mui/material";
import { PersonOutlined } from '@mui/icons-material';

import "swiper/css";
import "swiper/css/lazy"
import "swiper/css/navigation"
import "swiper/css/pagination"

import SwiperContainer from "../SwiperContainer";
import SwiperCore, { Lazy, Pagination } from 'swiper';

SwiperCore.use([Lazy, Pagination]);

class AppTeam extends Component {
    constructor(props) {
        super(props);
        this.state = {
            teamsList: [],
            isLoaded: false,
            thumbWidth: 75,
        }
    }

    componentDidMount() {
        this.teamsData().then(r => {
            this.setState({ isLoaded: true, teamsList: r.data });
        });
    }

    teamsData = async () => {
        return await axios.get("/api/teams/");
    };

    render() {
        const slides = [];
        const { isLoaded, teamsList } = this.state;
        if (isLoaded) {
            teamsList.map((team, index) => (
                slides.push(
                    <SwiperSlide key={`slide-${index}`}>
                        <Card className="w-100 h-100">
                            {team.profile ?
                                (
                                    <Box>
                                        <CardMedia
                                            component="img"
                                            alt={team.name}
                                            style={{ "objectFit": "contain" }}
                                            height={140}
                                            image={team.profile}
                                            title={team.name}
                                            className="swiper-lazy"
                                        />
                                        <div className="swiper-lazy-preloader swiper-lazy-preloader-black" />
                                    </Box>
                                ) : (<PersonOutlined color={"action"} style={{ fontSize: "140px" }} />)
                            }
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="div">
                                    {team.name}
                                </Typography>
                                <Typography variant="body2" color="textSecondary">
                                    {team.job}
                                </Typography>
                            </CardContent>
                        </Card>
                    </SwiperSlide>
                )
            ));
        }
        if (!isLoaded) {
            return (
                <Grid container spacing={4}>
                    {Array.from(new Array(4)).map((el, i) =>
                        <Grid item xs={12} sm={6} md={3} key={i}>
                            <Box sx={{ pt: 0.5 }}>
                                <Skeleton variant="rectangular" width="100%" height={200} />
                                <Skeleton width="40%" />
                                <Skeleton width="60%" />
                            </Box>
                        </Grid>
                    )}
                </Grid>
            )
        } else {
            return (
                teamsList.length > 0 ? (
                    <SwiperContainer slides={slides} />
                ) : ('')
            );
        }
    }
}

export default AppTeam;