import React, { Component } from "react";
import axios from "axios";
import { Box, Grid } from "@material-ui/core";
import { Skeleton } from "@mui/material";
import ServiceItem from "./ServiceItem";

class AppService extends Component {
    constructor(props) {
        super(props);
        this.state = {
            serviceList: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        this.serviceData().then(r => {
            this.setState({ isLoaded: true, serviceList: r.data });
        });
    }

    serviceData = async () => {
        return await axios.get("/api/services/");
    };

    render() {
        const { isLoaded, serviceList } = this.state;
        if (!isLoaded) {
            return (
                <Grid container spacing={3}>
                    {Array.from(new Array(3)).map((el, i) =>
                        <Grid item xs={12} sm={6} md={4} key={i}>
                            <Box sx={{ pt: 0.5 }}>
                                <Skeleton variant="rectangular" width={"100%"} height={150} />
                                <Skeleton width="60%" />
                                <Skeleton />
                            </Box>
                        </Grid>
                    )}
                </Grid>
            )
        } else {
            return (
                serviceList.length > 0 ? (
                    <Grid container spacing={2}>
                        {serviceList.map(item => <ServiceItem key={item.id} service={item} />)}
                    </Grid>
                ) : ('')
            );
        }
    }
}

export default AppService;