import React, { Component } from 'react';
import Container from '@mui/material/Container';
import axios from 'axios';
import AppWhyChose from './WhyChose/AppWhyChose';
import AppAboutUs from './AboutUs/AppAboutUs';
import AppService from './Service/AppService';
import AppLatestWork from './Service/AppLatestWork';
import AppMoreWork from './Service/AppMoreWork';
import AppStatistic from './Statistic/AppStatistic';
import AppWhatCustomersSay from './WhatCustomersSay/AppWhatCustomersSay';
import AppTeam from './Team/AppTeam';
import AppOurPartner from './OurPartner/AppOurPartner';
import AppCarousel from './Carousel/AppCarousel';
import SiteSection from './SiteSection';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            siteSections: [],
            isLoaded: false,
        };
    }

    componentDidMount() {
        this.sectionData();
    }

    sectionData = () => {
        axios.get('/api/sitesections/').then((res) => {
            this.setState({ isLoaded: true, siteSections: res.data });
        }).catch((err) => console.log(err));
    };

    render() {
        const { siteSections, isLoaded } = this.state;
        const sections = {};
        if (isLoaded) {
            siteSections.forEach((section) => {
                sections[section.section_type] = section;
            });
        }

        return (
            <div>
                <AppCarousel />
                <Container component='div'>
                    <div className='mt-2'>
                        {sections.why_chose ? (
                            <SiteSection
                                title={sections.why_chose.title}
                                subtitle={sections.why_chose.subtitle}
                                description={sections.why_chose.description}
                            />
                        ) : ''}
                        <AppWhyChose />
                    </div>
                    <div className='mt-2'>
                        {sections.aboutus ? (
                            <SiteSection
                                title={sections.aboutus.title} subtitle={sections.aboutus.subtitle}
                                description={sections.aboutus.description}
                            />
                        ) : ''}
                        <AppAboutUs />
                    </div>
                    <div className='mt-2'>
                        {sections.services ? (
                            <SiteSection
                                title={sections.services.title}
                                subtitle={sections.services.subtitle}
                                description={sections.services.description}
                            />
                        ) : ''}
                        <AppService />
                    </div>
                    <div className='mt-2'>
                        {sections.latestwork ? (
                            <SiteSection
                                title={sections.latestwork.title}
                                subtitle={sections.latestwork.subtitle}
                                description={sections.latestwork.description}
                            />
                        ) : ''}
                        <AppLatestWork />
                    </div>
                    <div className='mt-2'>
                        {sections.morework ? (
                            <SiteSection
                                title={sections.morework.title}
                                subtitle={sections.morework.subtitle}
                                description={sections.morework.description}
                            />
                        ) : ''}
                        <AppMoreWork />
                    </div>
                    <div className='mt-2'>
                        <AppStatistic />
                    </div>
                    <div className='mt-1'>
                        {sections.whatcustomersay ? (
                            <SiteSection
                                title={sections.morework.title}
                                subtitle={sections.morework.subtitle}
                                description={sections.morework.description}
                            />
                        ) : ''}
                        <AppWhatCustomersSay />
                    </div>
                    <div className='mt-1'>
                        {sections.ourteam ? (
                            <SiteSection
                                title={sections.ourteam.title}
                                subtitle={sections.ourteam.subtitle}
                                description={sections.ourteam.description}
                            />
                        ) : ''}
                        <AppTeam />
                    </div>
                    <div className='mt-2'>
                        {sections.ourpartner ? (
                            <SiteSection
                                title={sections.ourpartner.title}
                                subtitle={sections.ourpartner.subtitle}
                                description={sections.ourpartner.description}
                            />
                        ) : ''}
                        <AppOurPartner />
                    </div>
                </Container>
            </div>
        );
    }
}

export default Home;
