import React, { Component } from "react";
import axios from "axios";
import { Grid, Box, Skeleton } from "@mui/material";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import { Markup } from "interweave";
import Card from "@mui/material/Card";
import { Swiper, SwiperSlide } from "swiper/react";
import CardMedia from "@mui/material/CardMedia";

import "swiper/css";
import "swiper/css/lazy"
import "swiper/css/effect-cards"
import "swiper/css/navigation"
import "swiper/css/pagination"

import SwiperCore, { EffectCards, Lazy, Pagination } from 'swiper';

SwiperCore.use([Lazy, Pagination, EffectCards]);

class AppAboutUs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            aboutUsMessage: [], isLoaded: false
        }
    }

    componentDidMount() {
        this.aboutUsData().then(r => {
            this.setState({ isLoaded: true, aboutUsMessage: r.data });
        });
    }

    aboutUsData = async () => {
        return await axios.get("/api/aboutusmessage/");
    };

    render() {
        const { isLoaded, aboutUsMessage } = this.state;
        if (!isLoaded) {
            return (<Box
                sx={{
                    p: 2, width: '100%', borderRadius: '10px', display: 'flex', justifyContent: 'center',
                }}>
                <Skeleton
                    className="rounded"
                    variant="rectangular"
                    width="80%"
                    height={120} />
            </Box>)
        } else {
            return (aboutUsMessage.length > 0 ? (
                <Card sx={{ display: 'flex' }} className={"w-100 h-100"} elevation={0}>
                    <Grid container>
                        <Grid item xs={12} sm={12} md={8}>
                            <CardContent sx={{ flex: '1 0 auto' }}>
                                <CardMedia
                                    component="img"
                                    style={{ maxHeight: '300px' }}
                                    image={aboutUsMessage[0].image}
                                    alt={aboutUsMessage[0].label} />
                                <Typography component="div" variant="h5">
                                    {aboutUsMessage[0].label}
                                </Typography>
                                <Typography variant="subtitle1" color="textSecondary" component="div">
                                    <Markup content={aboutUsMessage[0].description} />
                                </Typography>
                            </CardContent>
                        </Grid>
                        <Grid item xs={12} sm={12} md={4}>
                            <CardContent sx={{ flex: '1 0 auto' }}>
                                <Swiper dir="rtl" lazy={true} effect={'cards'} grabCursor={true} className="mySwiper">
                                    {aboutUsMessage[0].points.map((point, index) => (
                                        <SwiperSlide key={index}>
                                            <Card className={"w-100 h-100"}>
                                                <Box>
                                                    <CardMedia
                                                        component="img"
                                                        style={{ maxHeight: '200', "objectFit": "revert" }}
                                                        image={point.image}
                                                        alt={point.label}
                                                        className="swiper-lazy"
                                                    />
                                                    <div
                                                        className="swiper-lazy-preloader swiper-lazy-preloader-black" />
                                                </Box>
                                                {point.title ?
                                                    <CardContent>
                                                        <Typography gutterBottom variant="h3" component="div">
                                                            {point.label}
                                                        </Typography>
                                                        <Typography gutterBottom variant="h5" component="div">
                                                            {point.title}
                                                        </Typography>
                                                        <Typography variant="body2" color="textSecondary">
                                                            {point.description}
                                                        </Typography>
                                                    </CardContent>
                                                    : ''}
                                            </Card>
                                        </SwiperSlide>
                                    ))}
                                </Swiper>
                            </CardContent>
                        </Grid>
                    </Grid>
                </Card>) : (''));
        }
    }
}

export default AppAboutUs;