import React, { Component } from "react";
import axios from "axios";
import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/css";
import "swiper/css/lazy";
import "swiper/css/pagination";
import "swiper/css/navigation";

import '../../index.css';

import SwiperCore, { Autoplay, Lazy, Navigation, Pagination } from 'swiper';
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { Skeleton, Box, Grid } from "@mui/material";
import { styled } from '@mui/material/styles';
import Stack from '@mui/material/Stack';

const ImageBox = styled(Box)(({ theme }) => ({
    position: 'relative',
    height: 500,
    [theme.breakpoints.down('sm')]: {
        width: '100% !important', // Overrides inline-style
        height: 200,
    },
}));

const ImageSrc = styled('img')({
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: 'cover',
    backgroundPosition: 'center 40%',
});

const ContainerBox = styled(Stack)(({ theme }) => ({
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: theme.palette.common.white,
}));

// const ImageBackdrop = styled('span')(({ theme }) => ({
//     position: 'absolute',
//     left: 0,
//     right: 0,
//     top: 0,
//     bottom: 0,
//     backgroundColor: theme.palette.common.black,
//     opacity: 0.4,
//     transition: theme.transitions.create('opacity'),
// }));

SwiperCore.use([Lazy, Autoplay, Pagination, Navigation]);

class AppCarousel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            carouselsList: [],
            isLoaded: false,
        }
    }

    componentDidMount() {
        this.carouselsData().then(r => {
            this.setState({ isLoaded: true, carouselsList: r.data });
        });
    }

    carouselsData = async () => {
        return await axios.get("/api/carousels/");
    };

    render() {
        const slides = [];
        const { isLoaded, carouselsList } = this.state;
        if (isLoaded) {
            carouselsList.map((carousel, index) => (
                slides.push(
                    <SwiperSlide key={`slide-${index}`} tag="li">
                        <Box sx={{ display: 'flex', flexWrap: 'wrap', maxHeight: "500px", minWidth: 40, width: '100%' }}>
                            <ImageBox
                                focusRipple
                                style={{
                                    width: "100%",
                                }}>
                                <ImageSrc src={carousel.image} className="swiper-lazy" />
                                <div className="swiper-lazy-preloader swiper-lazy-preloader-black" />
                                {/* <ImageBackdrop className="MuiImageBackdrop-root" /> */}
                                <ContainerBox>
                                    <Grid container>
                                        <Grid item xs={12} sm={12} md={12}>
                                            <Typography variant="h2">
                                                {carousel.title}
                                            </Typography>
                                            <Typography variant="h5">
                                                {carousel.description}
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={12} sm={12} md={12} className={'mt-4'}>
                                            {carousel.actions.map((element, index) => {
                                                return (<Button key={index} href={element.action} color={element.button} variant={element.outline ? 'outlined' : 'contained'} className="m-1">{element.label}</Button>);
                                            })}
                                        </Grid>
                                    </Grid>
                                </ContainerBox>
                            </ImageBox>
                        </Box>
                    </SwiperSlide>
                )
            ));
        }
        if (!isLoaded) {
            return (
                <Box
                    sx={{
                        width: '100%',
                        borderRadius: '10px',
                        display: 'flex',
                        justifyContent: 'center',
                    }}>
                    <Skeleton
                        animation={'wave'}
                        className="rounded"
                        variant="rectangular"
                        width="100%"
                        height={400} />
                </Box>
            )
        } else {
            return (
                carouselsList.length > 0 ? (
                    <Swiper dir="rtl" lazy={true} spaceBetween={30} centeredSlides={true} autoplay={{
                        "delay": 2500,
                        "disableOnInteraction": false
                    }} pagination={{
                        "dynamicBullets": true,
                        "clickable": true
                    }} navigation={true} className="main-swiper mt-5">
                        {slides}
                    </Swiper>
                ) : ('')
            );
        }
    }
}

export default AppCarousel;