import React, { Component } from "react";
import axios from "axios";
import { SwiperSlide } from "swiper/react";
import { Box, Card, CardContent, CardMedia, Grid, Typography, Skeleton } from "@mui/material";
import "swiper/css";
import "swiper/css/lazy"
import "swiper/css/navigation"
import "swiper/css/pagination"

import SwiperContainer from "../SwiperContainer";
import SwiperCore, { Lazy, Pagination } from 'swiper';

SwiperCore.use([Lazy, Pagination]);

class AppWhatCustomersSay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            whatCustomerSayList: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        this.whatCustomerSayData().then(r => {
            this.setState({ isLoaded: true, whatCustomerSayList: r.data });
        });
    }

    whatCustomerSayData = async () => {
        return await axios.get("/api/whatcustomerssay/");
    };

    render() {
        const slides = [];
        const { isLoaded, whatCustomerSayList } = this.state;
        if (isLoaded) {
            whatCustomerSayList.map((item, index) => (
                slides.push(
                    <SwiperSlide key={`slide-${index}`}>
                        <Card className="w-100 h-100">
                            <Box>
                                <CardMedia
                                    component="img"
                                    alt={item.title}
                                    style={{ "objectFit": "cover" }}
                                    height="300"
                                    image={item.image}
                                    title={item.title}
                                    className="swiper-lazy"
                                />
                                <div className="swiper-lazy-preloader swiper-lazy-preloader-black" />
                            </Box>
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="div">
                                    {item.title}
                                </Typography>
                                <Typography variant="body2" color="textSecondary">
                                    {item.description}
                                </Typography>
                            </CardContent>
                        </Card>
                    </SwiperSlide>
                )
            ));
        }
        if (!isLoaded) {
            return (
                <Grid container spacing={4}>
                    {Array.from(new Array(4)).map((el, i) =>
                        <Grid item xs={12} sm={6} md={3} key={i}>
                            <Box sx={{ pt: 0.5 }}>
                                <Skeleton variant="rectangular" width="100%" height={300} />
                                <Skeleton width="100%" />
                                <Skeleton width="100%" />
                            </Box>
                        </Grid>
                    )}
                </Grid>
            )
        } else {
            return (
                whatCustomerSayList.length > 0 ? (
                    <SwiperContainer slides={slides} />
                ) : ('')
            );
        }
    }
}

export default AppWhatCustomersSay;