import React, { Component } from "react";
import axios from "axios";
import { Box, Grid } from "@material-ui/core";
import { Skeleton } from "@mui/material";
import WhyChoseItem from "./WhyChoseItem";

class AppWhyChose extends Component {
    constructor(props) {
        super(props);
        this.state = {
            whyChoseList: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        this.whyChoseData().then(r => {
            this.setState({ isLoaded: true, whyChoseList: r.data });
        });
    }

    whyChoseData = async () => {
        return await axios.get("/api/whychoses/");
    };

    render() {
        const { isLoaded, whyChoseList } = this.state;
        if (!isLoaded) {
            return (
                <Grid container spacing={3}>
                    {Array.from(new Array(4)).map((el, i) =>
                        <Grid item xs={12} sm={6} md={3} key={i}>
                            <Box sx={{ pt: 0.5 }}>
                                <Skeleton variant="rectangular" width={"100%"} height={118} />
                                <Skeleton />
                                <Skeleton width="60%" />
                            </Box>
                        </Grid>
                    )}
                </Grid>
            )
        } else {
            return (
                whyChoseList.length > 0 ? (
                    <Grid container spacing={2}>
                        {whyChoseList.map(item => <WhyChoseItem key={item.id} item={item} />)}
                    </Grid>
                ) : ('')
            );
        }
    }
}

export default AppWhyChose;