import React, { Component } from 'react';
import axios from 'axios';
import { Swiper, SwiperSlide } from 'swiper/react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import 'swiper/css';
import 'swiper/css/lazy';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

import SwiperCore, { Lazy, Pagination, Navigation } from 'swiper';
import { Box } from '@mui/material';

SwiperCore.use([Lazy, Pagination, Navigation]);

class AppLatestWork extends Component {
    constructor(props) {
        super(props);
        this.state = {
            latestWorkList: [], isLoaded: false
        }
    }

    componentDidMount() {
        this.latestWorkData().then(r => {
            this.setState({ isLoaded: true, latestWorkList: r.data });
        });
    }

    latestWorkData = async () => {
        return await axios.get('/api/latestworks/');
    };

    render() {
        const slides = [];
        const { isLoaded, latestWorkList } = this.state;
        if (isLoaded) {
            latestWorkList.map((item, index) => (slides.push(<SwiperSlide key={`slide-${index}`}>
                <Card className={'w-100 h-100'}>
                    <Box>
                        <CardMedia
                            component='img'
                            image={item.image}
                            style={{
                                objectFit: 'cover',
                            }}
                            height={250}
                            alt={item.title}
                            className='swiper-lazy'
                        />
                        <div className='swiper-lazy-preloader swiper-lazy-preloader-black' />
                    </Box>
                    {item.title || item.description ? (<CardContent>
                        {item.title ?
                            <Typography gutterBottom variant='h5' component='h5'>
                                {item.title}
                            </Typography> : ('')}
                        {item.description ? (<Typography variant='body2' color='textSecondary' component='p'>
                            {item.description}
                        </Typography>) : ('')}
                    </CardContent>) : ('')}
                </Card>
            </SwiperSlide>)));
        }
        return (latestWorkList.length > 0 ? (
            <Swiper dir='rtl' lazy={true} navigation={true} slidesPerView={1} pagination={{
                'type': 'progressbar'
            }} spaceBetween={30} breakpoints={{
                // when window width is >= 320px
                320: {
                    slidesPerView: 2, spaceBetween: 10,
                }, // when window width is >= 640px
                640: {
                    slidesPerView: 2, spaceBetween: 10
                }, // when window width is >= 768px
                768: {
                    slidesPerView: 3, spaceBetween: 10
                }, // when window width is >= 1024px
                1024: {
                    slidesPerView: 4, spaceBetween: 10
                },
            }} className='latest-works-swiper pt-1 pb-1'>
                {slides}
            </Swiper>) : (''));
    }
}

export default AppLatestWork;