import React, { Component } from 'react';
import axios from 'axios';
import { Box, Grid } from '@material-ui/core';
import { Skeleton } from '@mui/material';
import { styled } from '@mui/system';
import TabsUnstyled from '@mui/base/TabsUnstyled';
import TabsListUnstyled from '@mui/base/TabsListUnstyled';
import TabPanelUnstyled from '@mui/base/TabPanelUnstyled';
import { buttonUnstyledClasses } from '@mui/base/ButtonUnstyled';
import TabUnstyled, { tabUnstyledClasses } from '@mui/base/TabUnstyled';
import MoreWorkItem from './MoreWorkItem';

const blue = {
    50: '#F0F7FF',
    100: '#C2E0FF',
    200: '#80BFFF',
    300: '#66B2FF',
    400: '#3399FF',
    500: '#007FFF',
    600: '#0072E5',
    700: '#0059B2',
    800: '#004C99',
    900: '#003A75',
};

const Tab = styled(TabUnstyled)`
  font-family: IBM Plex Sans, sans-serif;
  color: white;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: bold;
  background-color: transparent;
  padding: 5px 5px;
  margin: 6px 6px;
  border: none;
  border-radius: 40px;
  display: flex;

  &:hover {
    background-color: ${blue[400]};
  }

  &.${buttonUnstyledClasses.focusVisible} {
    color: #fff;
    outline: none;
    background-color: ${blue[200]};
  }

  &.${tabUnstyledClasses.selected} {
    background-color: ${blue[50]};
    color: ${blue[600]};
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const TabPanel = styled(TabPanelUnstyled)`
  width: 100%;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 0.875rem;
`;

const TabsList = styled(TabsListUnstyled)`
  min-width: 320px;
  background-color: ${blue[500]};
  border-radius: 50px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: space-between;
`;


class AppMoreWork extends Component {
    constructor(props) {
        super(props);
        this.state = {
            webMoreWorks: [],
            wisLoaded: false,
            appsMoreWorks: [],
            aisLoaded: false,
            storesMoreWorks: [],
            sisLoaded: false,
            businessMoreWorks: [],
            biisLoaded: false,
            webHostingMoreWorks: [],
            whisLoaded: false,
            graphicsMoreWorks: [],
            gisLoaded: false,
            digitalMoreWorks: [],
            disLoaded: false,
            commercialMoreWorks: [],
            caisLoaded: false,
            computerMoreWorks: [],
            cisLoaded: false,
            mediaMoreWorks: [],
            mpisLoaded: false,
            othersMoreWorks: [],
            oisLoaded: false,
        }
    }

    componentDidMount() {
        this.webMoreWorksData().then(r => {
            this.setState({ wisLoaded: true, webMoreWorks: r.data });
        });
        this.appsMoreWorksData().then(r => {
            this.setState({ aisLoaded: true, appsMoreWorks: r.data });
        });
        this.storesMoreWorksData().then(r => {
            this.setState({ sisLoaded: true, storesMoreWorks: r.data });
        });
        this.businessMoreWorksData().then(r => {
            this.setState({ biisLoaded: true, businessMoreWorks: r.data });
        });
        this.webHostingMoreWorksData().then(r => {
            this.setState({ whisLoaded: true, webHostingMoreWorks: r.data });
        });
        this.graphicsMoreWorksData().then(r => {
            this.setState({ gisLoaded: true, graphicsMoreWorks: r.data });
        });
        this.digitalMoreWorksData().then(r => {
            this.setState({ gisLoaded: true, graphicsMoreWorks: r.data });
        });
        this.commercialMoreWorksData().then(r => {
            this.setState({ caisLoaded: true, commercialMoreWorks: r.data });
        });
        this.mediaMoreWorksData().then(r => {
            this.setState({ mpisLoaded: true, mediaMoreWorks: r.data });
        });
        this.othersMoreWorksData().then(r => {
            this.setState({ oisLoaded: true, othersMoreWorks: r.data });
        });
    }

    webMoreWorksData = async () => {
        return await axios.get('/api/wdmoreworks/');
    };

    appsMoreWorksData = async () => {
        return await axios.get('/api/admoreworks/');
    };

    storesMoreWorksData = async () => {
        return await axios.get('/api/sdmoreworks/');
    };

    businessMoreWorksData = async () => {
        return await axios.get('/api/bidmoreworks/');
    };

    webHostingMoreWorksData = async () => {
        return await axios.get('/api/whmoreworks/');
    };

    graphicsMoreWorksData = async () => {
        return await axios.get('/api/gdmoreworks/');
    };

    digitalMoreWorksData = async () => {
        return await axios.get('/api/dpdmoreworks/');
    };

    commercialMoreWorksData = async () => {
        return await axios.get('/api/cadmoreworks/');
    };

    computerMoreWorksData = async () => {
        return await axios.get('/api/cdmoreworks/');
    };

    mediaMoreWorksData = async () => {
        return await axios.get('/api/mpdmoreworks/');
    };

    othersMoreWorksData = async () => {
        return await axios.get('/api/odmoreworks/');
    };


    render() {

        const { wisLoaded, webMoreWorks, appsMoreWorks, storesMoreWorks, businessMoreWorks, webHostingMoreWorks, graphicsMoreWorks, digitalMoreWorks, commercialMoreWorks, computerMoreWorks, mediaMoreWorks, othersMoreWorks } = this.state;
        if (!wisLoaded) {
            return (
                <Grid container spacing={3}>
                    {Array.from(new Array(3)).map((el, i) =>
                        <Grid item xs={12} sm={6} md={4} key={i}>
                            <Box sx={{ pt: 0.5 }}>
                                <Skeleton variant='rectangular' width={'100%'} height={150} />
                                <Skeleton width='60%' />
                                <Skeleton />
                            </Box>
                        </Grid>
                    )}
                </Grid>
            )
        } else {
            return (
                <TabsUnstyled defaultValue={0}>
                    <TabsList style={{ overflow: 'auto' }}>
                        {webMoreWorks.length > 0 ? (
                            <Tab>تطوير الويب</Tab>
                        ) : ('')}
                        {appsMoreWorks.length > 0 ? (
                            <Tab>تطوير التطبيقات</Tab>
                        ) : ('')}
                        {storesMoreWorks.length > 0 ? (
                            <Tab>تصميم المتاجر</Tab>
                        ) : ('')}
                        {businessMoreWorks.length > 0 ? (
                            <Tab>تصميم الهوية التجارية</Tab>
                        ) : ('')}
                        {webHostingMoreWorks.length > 0 ? (
                            <Tab>استضافه المواقع</Tab>
                        ) : ('')}
                        {graphicsMoreWorks.length > 0 ? (
                            <Tab>تصميم الجرافيكس</Tab>
                        ) : ('')}
                        {digitalMoreWorks.length > 0 ? (
                            <Tab> NFT تصميم الصور الرقمية</Tab>
                        ) : ('')}
                        {commercialMoreWorks.length > 0 ? (
                            <Tab>تصميم الإعلان التجاري</Tab>
                        ) : ('')}
                        {computerMoreWorks.length > 0 ? (
                            <Tab>تصميم أنظمة الكمبيوتر</Tab>
                        ) : ('')}
                        {mediaMoreWorks.length > 0 ? (
                            <Tab>تصميم منشور الوسائط</Tab>
                        ) : ('')}
                        {othersMoreWorks.length > 0 ? (
                            <Tab>اخرى</Tab>
                        ) : ('')}

                    </TabsList>
                    {webMoreWorks.length > 0 ? (
                        <TabPanel value={0}>
                            <Grid container spacing={2}>
                                {webMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {appsMoreWorks.length > 0 ? (
                        <TabPanel value={1}>
                            <Grid container spacing={2}>
                                {appsMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {storesMoreWorks.length > 0 ? (
                        <TabPanel value={2}>
                            <Grid container spacing={2}>
                                {storesMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {businessMoreWorks.length > 0 ? (
                        <TabPanel value={3}>
                            <Grid container spacing={2}>
                                {businessMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {webHostingMoreWorks.length > 0 ? (
                        <TabPanel value={4}>
                            <Grid container spacing={2}>
                                {webHostingMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {graphicsMoreWorks.length > 0 ? (
                        <TabPanel value={5}>
                            <Grid container spacing={2}>
                                {graphicsMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {digitalMoreWorks.length > 0 ? (
                        <TabPanel value={6}>
                            <Grid container spacing={2}>
                                {digitalMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {commercialMoreWorks.length > 0 ? (
                        <TabPanel value={7}>
                            <Grid container spacing={2}>
                                {commercialMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {computerMoreWorks.length > 0 ? (
                        <TabPanel value={8}>
                            <Grid container spacing={2}>
                                {computerMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {mediaMoreWorks.length > 0 ? (
                        <TabPanel value={9}>
                            <Grid container spacing={2}>
                                {mediaMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                    {othersMoreWorks.length > 0 ? (
                        <TabPanel value={10}>
                            <Grid container spacing={2}>
                                {othersMoreWorks.map(item => <MoreWorkItem key={item.id} work={item} />)}
                            </Grid>
                        </TabPanel>
                    ) : ('')}
                </TabsUnstyled>
            );
        }
    }
}

export default AppMoreWork;