import React, { Component } from "react";
import axios from "axios";
import { SwiperSlide } from "swiper/react";
import { Skeleton } from "@mui/material";
import Typography from "@mui/material/Typography";
import { Grid } from "@material-ui/core";
import SwiperContainer from "../SwiperContainer";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';

class AppStatistic extends Component {
    constructor(props) {
        super(props);
        this.state = {
            statisticsList: [],
            isLoaded: false,
        }
    }

    componentDidMount() {
        this.statisticsListData().then(r => {
            this.setState({ isLoaded: true, statisticsList: r.data });
        });
    }

    statisticsListData = async () => {
        return await axios.get("/api/statistics/");
    };

    render() {
        const slides = [];
        const { isLoaded, statisticsList } = this.state;
        if (isLoaded) {
            statisticsList.map((item, index) => (
                slides.push(
                    <SwiperSlide key={`slide-${index}`} tag="li">
                        <Card sx={{ minHeight: 200 }} className={'w-100 h-100 border-' + item.theme} variant="outlined">
                            <CardContent className={item.text_as_theme ? 'text-' + item.theme : ''}>
                                <Typography variant="h2" component="h2">
                                    {item.count}
                                </Typography>
                                <Typography sx={{ mb: 4 }} variant="h4">
                                    {item.title}
                                </Typography>
                                {item.description ? (
                                    <Typography variant="body2" color={'GrayText'}>
                                        {item.description}
                                    </Typography>
                                ) : ('')}
                            </CardContent>
                        </Card>
                    </SwiperSlide>
                )
            ));
        }
        if (!isLoaded) {
            const variants = ['h1', 'h3',];
            const skeletonSlides = [];
            Array.from(new Array(4)).map((el, i) =>
                skeletonSlides.push(
                    <SwiperSlide key={`slide-${i}`}>
                        <Grid item xs={12} sm={6} md={12} key={i}>{
                            variants.map((variant) => (
                                <Typography component="div" key={variant} variant={variant}>
                                    <Skeleton />
                                </Typography>
                            ))}
                        </Grid>
                    </SwiperSlide>
                )
            )
            return (
                <SwiperContainer navigation={false} slides={skeletonSlides} clickable={false} dynamicBullets={false} />
            )
        } else {
            return (
                statisticsList.length > 0 ? (
                    <SwiperContainer navigation={true} slides={slides} clickable={true} dynamicBullets={true} />
                ) : ('')
            );
        }
    }
}

export default AppStatistic;